import { UseContextData } from "../Context/UseContextData"
import FlatButton from "../shared/FlatButton"
import { FlatCart } from "../shared/FlatCart"
import { checkoutvalues } from "../shared/Types"
import {CreditCardOutlined} from '@ant-design/icons'
type props ={
    data:checkoutvalues
}
export const Payment = ({data}:props)=>{
    const {cart} = UseContextData();
    const styles = {
        content:{
            display:'flex',
            justifyContent:"space-between",
            alignItems:'center',
            margin:'20px 0'
        },
        container:{
            backgroundColor:'#FAFAFA',
            padding:'16px 30px',
            borderRadius:'20px'
        },

        logoContainer:{
            display: 'flex',
            alignItems: 'center',
            justifyContent:'space-between'
        },

        logo:{
            width: '55px',
            height: '50px',
        },
        card:{
            border:'solid 1px black',
            display:'flex',
            padding:'16px',
            borderRadius:'20px',
            margin:'20px 0'
        }
          
    }
    return (
        <div>
            <div style={{...styles.card , flexDirection:'column'}}>
                <div style={{...styles.logoContainer}}>
                    <h3>Pay stack</h3>
                    <div>
                        <img style={styles.logo} src="data:image/jpeg;base64," alt='visa'/>
                        <img style={styles.logo} src="data:image/png;base64," alt="paystack" />
                        <img style={styles.logo} src='data:image/png;base64,'alt="master card"/>
                    </div>
                </div>
                    <CreditCardOutlined style={{ fontSize:"200px",alignSelf:'center'}}/>
                    <p style={{textAlign:'center'}}>
                        After Clicking pay now you will be redirected to paystack to complete your payment securely
                    </p>
                    <FlatButton title="paynow" onClick={()=>console.log(data)}/>
                </div>
            
            <div>
                
                <div></div>
            <div style={styles.container}>
            
                    
                    <p style={styles.content}><strong>Name:</strong> {data.name}</p>
                    <p style={styles.content}><strong>Email:</strong>{data.email}</p>
                    <p style={styles.content}><strong>Phone:</strong> {data.phone}</p>
                    <p style={styles.content}><strong>Country:</strong> {data.country}</p>
                    <p style={styles.content}><strong>Address:</strong> {data.address}</p>
                    <p style={styles.content}><strong>State:</strong>{data.state}</p>
                    <p style={styles.content}><strong>City:</strong> {data.city}</p>

                    <h3>Items:</h3>
                    {
                    cart &&cart.map(item=>(
                        <div key={item.id} style={styles.content}>
                            <FlatCart item={item}/>
                        </div>
                        
                    ))
                    }
                </div>
                
            </div>
        </div>
    )
}